


























































import Vue from "vue";
import store from "@/store";
import TabelaInfo from "./TabelaInfo.vue";

const dataFormat = (data: Date) =>
  data.getDay() + "/" + data.getMonth() + "/" + data.getFullYear();

interface Configuracao {
  Id: string;
  Configuracao: string;
}

interface Configuracoes {
  TipoDeAeronave: string;
  Configuracoes: Configuracao[];
}

export default Vue.extend({
  name: "PayLoad",
  components: {
    TabelaInfo
  },
  data() {
    return {
      data: this.$store.state.data,
      aeronaves: [],
      aeronaveSelected: "",
      configuracoes: []
    };
  },
  created() {
    const login = () => store.getters.login;
    login();
    const configuracoes = () => store.getters.configuracoes;
    configuracoes().then((response: any) => {
      this.aeronaves = response;
      this.aeronaveSelected = response[0].TipoDeAeronave;
      this.configuracoes = response[0].Configuracoes;
    });
  },
  methods: {
    sendInfo() {
      this.$store.state.data.Data = dataFormat(
        new Date(this.$store.state.data.Data)
      );
      if (typeof this.$store.state.data.Escalas === "string") {
        this.$store.state.data.Escalas = this.$store.state.data.Escalas.split(
          ","
        );
      }
      this.$store.getters.pesoDisponivel(localStorage.getItem("token"));
    },
    changeConfig() {
      const selected = this.aeronaveSelected;
      const configuracao = this.aeronaves.filter((aeronave: Configuracoes) => {
        return aeronave.TipoDeAeronave === selected;
      })[0] as Configuracoes;
      this.configuracoes = configuracao.Configuracoes as any;
    }
  }
});
