import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const URL = "https://teste.sistemasol.com.br/";

export default new Vuex.Store({
  state: {
    data: {
      Data: "17/05/2018",
      ETD: "08:30",
      From: "SBME",
      To: "SBME",
      Tipo: "VFR",
      Temperatura: "26",
      Configuracoes: [],
      Escalas: [
        "P40",
        "P35",
        "P50",
      ]
    },
    roteiro: "",
    tiposAeronaveis: [],
    disponiveis: [],
    configuracoes: []
  },
  getters: {
    login: () => {
      if (!localStorage.getItem("token")) {
        fetch(URL + "api/login", {
          method: "POST",
          body: JSON.stringify({
            username: "valtim",
            password: "fodase"
          }),
          headers: new Headers({
            "Accept": "application/json, text/plain, */*",
            "Content-Type": "application/json"
          })
        }).then((res) => {
          return res.json();
        }).then((response) => {
          localStorage.setItem("token", response);
        }).catch((error) => {
          throw (error);
        });
      }
    },
    pesoDisponivel: (state) => (token: string) => {
      fetch(URL + "api/pesodisponivel/", {
        headers: {
          "content-type": "application/json",
          "token": token
        },
        method: "POST",
        body: JSON.stringify({
          Data: state.data.Data,
          ETD: "08:30",
          From: state.data.From,
          To: state.data.To,
          Tipo: state.data.Tipo,
          Temperatura: "26",
          Escalas: [
            "P40",
            "P35",
            "P50",
          ]
        })
      }).then((response) => response.json())
        .then((response) => {
          state.roteiro = response.Roteiro;
          state.disponiveis = response.Disponiveis;
          state.tiposAeronaveis = response.TiposDeAeronave;
        })
        .catch((error) => { throw (error.message); });
    },
    configuracoes: (state) => {
      return fetch(URL + "api/configuracoes").then((response) => response.json())
        .then((response) => {
          if (!localStorage.getItem("configuracoes")
            || JSON.stringify(localStorage.getItem("configuracoes")) !== JSON.stringify(response)) {
            localStorage.setItem("configuracoes", response);
            state.configuracoes = response;
          }
          return state.configuracoes;
        })
        .catch((error) => { throw (error.message); });
    }
  },
  mutations: {

  },
  actions: {

  }
});
