
















































import Vue from "vue";
import store from "@/store";

interface Disponivel {
  TipoAeronave: string;
  Configuracao: string;
  Prefixo: string;
  PesoDisponivel: number;
}

interface TipoAeronave {
  TipoDeAeronave: string;
  TempoDeVoo: string;
  TempoDeCorrecao: string;
  TempoDeReserva: string;
  TempoTotal: string;
  CombustivelMinimo: number;
}

export default Vue.extend({
  name: "TabelaInfo",
  data() {
    return {
      roteiro: this.$store.state.roteiro,
      tiposAeronaveis: this.$store.state.tiposAeronaveis,
      disponiveis: this.$store.state.disponiveis,
      legendas: [
        {
          code: "TempoDeVoo",
          name: "Tempo Voo"
        },
        {
          code: "TempoDeCorrecao",
          name: "T. Correção"
        },
        {
          code: "TempoDeReserva",
          name: "T. Reserva"
        },
        {
          code: "TempoTotal",
          name: "Tempo Total"
        },
      ]
    };
  },
  methods: {
    getValuePesoDisponivel(TipoDeAeronave: string, propriedade: string) {
      const valor = this.tiposAeronaveis.filter(
        (tipoAeronave: TipoAeronave) => {
          return tipoAeronave.TipoDeAeronave === TipoDeAeronave;
        }
      )[0];
      return valor[propriedade];
    }
  }
});
